import { Component, ViewChild } from '@angular/core';
import { ViewController,IonicPage,  NavController,Events , ActionSheetController, NavParams } from 'ionic-angular';
import {   FormBuilder,  FormGroup,   Validators } from '@angular/forms';
import { Auth,NotificationProvider } from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';

@IonicPage()
@Component({
  selector: 'page-empresa-create',   
  templateUrl: 'empresa-create.html',
})
export class EmpresaCreatePage {
  @ViewChild('fileInput') fileInput;
  public form                  : FormGroup;
  public nit                 : any;
  public nombre		     : any;
  public razon		     : any;
  public direccion		     : any;
  public email		     : any;
  public estado		     : any;
  public telefono		     : any;
  private _ID                  : String;
  idempresa:any;
  public image              	: any;
  public pageTitle           	: string;
  username: string = "";

  isDisablednit :any;
  constructor( public storage: Storage, public msj: NotificationProvider, public translateService: TranslateService,public viewCtrl: ViewController,public authService:  Auth,public navCtrl 		: NavController,  
    public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController, 
          public events: Events)
  {
    this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value
    });
     this.form = this._FB.group({
        nit        	   :  ['', Validators.compose([Validators.maxLength(8), Validators.pattern(/^\d{8}$/), Validators.required])],
        nombre     : ['', Validators.required],
        razon     : ['', Validators.required],
        direccion      : ['', Validators.required],
        email       : ['', Validators.compose([Validators.maxLength(50), Validators.pattern(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/), Validators.required])],
        telefono       : ['', Validators.compose([Validators.maxLength(8), Validators.pattern(/^\d{8}$/), Validators.required])],
        estado      : ['', Validators.required]
     });

  }

  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  }); 
  }

  ionViewDidLoad() : void
  {
    if(this.navParams.get("record")==null)
    {
       
      this.translateService.get('ITEM_CREATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
      
      this.isDisablednit =true
    }
    else {
      //this._ID 				=	this.navParams.data.record.nit;
       this._ID 				=	this.navParams.data.record._id;
       this.nit 				=	this.navParams.data.record.nit;
       this.nombre		=	this.navParams.data.record.nombre;
       this.razon			=	this.navParams.data.record.razon;
       this.direccion  			=	this.navParams.data.record.direccion;
       this.email			=	this.navParams.data.record.email;
       this.telefono		=	this.navParams.data.record.telefonos;
       this.estado		=	this.navParams.data.record.estado;
       this.isDisablednit =false
       this.translateService.get('ITEM_UPDATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
    

    }
  }

  manageGallery() : void
  {
    let creat='Actualiza';
    if(this.navParams.get("record")==null)
    {  creat='Crea';
    }
    
     let   options       : any	     = { 
       idempresa0:this.idempresa,
     nit : this.form.controls['nit'].value,
     nombre : this.form.controls['nombre'].value,
     razon : this.form.controls['razon'].value,
     direccion : this.form.controls['direccion'].value,
     email : this.form.controls['email'].value,
     telefonos : this.form.controls['telefono'].value,
     estado : this.form.controls['estado'].value,
     bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' empresas '}
    };
      

     if(this.navParams.get("record")==null)
     {  
          if(options){
            this.authService.createReg('',options,'empresas').then((result) => {
              if (!this.form.valid) { return; }
              this.viewCtrl.dismiss(result);
          }, (err) => {
            this.msj.showError3('Error',err.error);
          });
          }
     }   
     else
     {
      if(options){
        this.authService.createReg(this._ID,options,'empresas').then((result) => {
           if (!this.form.valid) { return; }
           this.viewCtrl.dismiss(result);
       }, (err) => {
        this.msj.showError3('Error',err.error);
       });
    }}
  }

  cancel() {
    this.viewCtrl.dismiss();
  }
  done() {
    if (!this.form.valid) { return; }
    //this.viewCtrl.dismiss(this.form.value);
  }



}



